@font-face {
  font-family: 'fontello';
  src: url(/static/media/fontello.e955c830.eot);
  src: url(/static/media/fontello.e955c830.eot#iefix) format('embedded-opentype'),
       url(/static/media/fontello.c4ae34d8.woff2) format('woff2'),
       url(/static/media/fontello.a851bc33.woff) format('woff'),
       url(/static/media/fontello.e3de01ee.ttf) format('truetype'),
       url(/static/media/fontello.086acf32.svg#fontello) format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
